import React from 'react'
import Helmet from 'react-helmet'
import styled from 'styled-components'
import PostListing from '../components/PostListing/PostListing'

import SEO from '../components/SEO'
import config from '../../data/SiteConfig'
import SiteHeader from '../components/Layout/Header'

class Blog extends React.Component {
  render() {
    const allSEOMarkdown = this.props.data.allMarkdown.edges
    const postEdges = this.props.data.posts.edges
    return (
      <div className="blog-container">
        <Helmet title={'The Optic Blog'} />
        <SEO postEdges={allSEOMarkdown} />
        <main>
          <HeaderContainer>
            <SiteHeader location={this.props.location} />
          </HeaderContainer>
          <IndexHeadContainer>
            <Hero>
              <img src={config.siteLogo} height="110px" alt="" />
              <h1 style={{color: 'white'}}>{'Welcome to our Blog'}</h1>
            </Hero>
          </IndexHeadContainer>
          <BodyContainer>
            <PostListing postEdges={postEdges} />
          </BodyContainer>
        </main>
      </div>
    )
  }
}

export default Blog

const IndexHeadContainer = styled.div`
  background: ${props => props.theme.brand};
  padding: ${props => props.theme.sitePadding};
  text-align: center;
`
const HeaderContainer = styled.div`
  grid-column: 1 / 3;
  grid-row: 1 / 2;
  z-index: 2;
  position: fixed;
  width: 100%;
  
  @media screen and (max-width: 600px) {
    order: 1;
  }
`

const Hero = styled.div`
  padding: 50px 0;
  & > h1 {
    font-weight: 600;
  }
`

const BodyContainer = styled.div`
  padding: ${props => props.theme.sitePadding};
  max-width: ${props => props.theme.contentWidthLaptop};
  margin: 0 auto;
`

export const pageQuery = graphql`
  query BlogQuery {
    allMarkdown: allMarkdownRemark(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
    ) {
      edges {
        node {
          fields {
            slug
          }
          excerpt
          timeToRead
          frontmatter {
            title
            tags
            cover
            date
          }
        }
      }
    }
    posts: allMarkdownRemark(
      limit: 2000
      filter: { frontmatter: { type: { eq: "post" } } }
      sort: { fields: [frontmatter___date], order: DESC }
    ) {
      edges {
        node {
          fields {
            slug
          }
          excerpt
          timeToRead
          frontmatter {
            title
            tags
            cover
            month: date(formatString: "MMMM")
            dateOfMonth: date(formatString: "Do")
          }
        }
      }
    }
  }
`



