import React from 'react'
import Link from 'gatsby-link'
import styled from 'styled-components'

class PostListing extends React.Component {
  getPostList() {
    const postList = []
    this.props.postEdges.forEach(postEdge => {
      postList.push({
        path: postEdge.node.fields.slug,
        tags: postEdge.node.frontmatter.tags,
        cover: postEdge.node.frontmatter.cover,
        title: postEdge.node.frontmatter.title,
        date: postEdge.node.frontmatter.date,
        month: postEdge.node.frontmatter.month,
        dateOfMonth: postEdge.node.frontmatter.dateOfMonth,
        excerpt: postEdge.node.excerpt,
        timeToRead: postEdge.node.timeToRead
      })
    })
    return postList
  }
  render() {
    const postList = this.getPostList()
    return (
      <PostListingContainer>
        {/* Your post list here. */
        postList.map(post => (
          <PostListingItem>
            <div className="post-date">
              <p className="post-date-month">{post.month}</p>
              <p className="post-date-day">{post.dateOfMonth}</p>
            </div>
            <Link to={post.path} key={post.title}>
              <div style={{fontSize: 30, lineHeight: '1.2em', fontWeight: 200}}>{post.title}</div>
              <p className="post-excerpt">{post.excerpt}</p>
              <p className="post-time-to-read">Est. Reading: {post.timeToRead} mins.</p>
            </Link>
          </PostListingItem>
        ))}
      </PostListingContainer>
    )
  }
}

const PostListingContainer = styled.div`
  display: flex;
  flex-direction: row;
  text-align: center;
  flex-wrap: wrap;
  padding-top: 20px;
  
  p {
    margin-bottom: 10px;
    text-align: justify;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @media screen and (max-width:600px) {
    flex-wrap: wrap;
  }
`

const PostListingItem = styled.div`
  display: flex;
  flex-direction: row;
  flex: 0 1 100%;
  margin-bottom: 60px;
  text-align: left;

  .post-date {
    flex: 0 1 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    // background: radial-gradient(#399cd3, #60baec);
    // background: #60baec;
    background: linear-gradient(#399cd3, #60baec);
    color: #FFF;
    padding: 10px 0;
    margin-right: 40px;

    p {
      margin: 0;

      &.post-date-month {
        font-size: 1.5rem;
      }
      &.post-date-day {
        font-size: 4rem;
      }
    }
  }
  > a {
    flex: 0 1 80%;
  }

  @media screen and (max-width:600px) {
    margin-left: 0;
    flex-direction: column;

    .post-date {
      flex: 0 1 100%;
      margin-right: 0;
      margin-bottom: 15px;
    }
    > a {
      flex: 0 1 100%;
    }
  }
`

export default PostListing



